@import "https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap";
body * {
  font-family: Inter;
}

.form-wrapper {
  width: 340px;
  min-height: 460px;
  background: #fff;
  border-radius: 12px;
  flex-direction: column;
  margin: 0 auto;
  padding: 50px 30px 30px;
  display: flex;
  box-shadow: 0 0 6px #00000024;
}

.input {
  position: relative;
}

.input input {
  width: 100%;
  border: none;
  border-bottom: 1px solid #3369f3;
  outline: none;
  padding-top: 8px;
  padding-bottom: 7px;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
}

.input label {
  color: #999;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.input p {
  color: #ff2f2f;
  font-size: 8px;
  font-weight: 400;
  line-height: 8px;
  display: none;
  position: absolute;
  top: calc(100% + 7px);
}

.input.active label {
  font-size: 9px;
  line-height: 9px;
  transition: all .5s;
  top: 0;
}

.button {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background: #3369f3;
  border: none;
  border-radius: 8px;
  padding: 12px 0;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
}

.link {
  color: #3369f3;
  font-size: 11px;
  font-weight: 500;
  line-height: 11px;
  text-decoration: none;
}

.link.red {
  color: red;
}

.error {
  width: 100%;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.error h1 {
  color: #1e1e1e;
  margin-bottom: 20px;
  font-size: 40px;
  font-weight: 500;
  line-height: 40px;
}

.error h2 {
  color: #1e1e1e;
  margin-bottom: 69px;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
}

